<template>
  <div id="dashboard">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>Companies</h4>
      </li>
      <li v-for="company in companies" v-bind:key="company.id" class="collection-item">
        <div class="chip">
          {{company.industry}}
        </div>
        {{company.name}}
        ({{company.url}})
        <router-link class="secondary-content" v-bind:to="{name: 'view-company', params: {company_id: company.company_id}}">
          <i class="fa fa-eye"></i>
        </router-link>
      </li>
    </ul>
    <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-large red">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'dashboard',
  data () {
    return {
      companies: []
    }
  },
  //auto run component when loaded
  created () {
    db.collection('Companies').orderBy('industry').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        const data = {
          'id': doc.id, //firebase id
          'company_id': doc.data().company_id, //our id
          'name': doc.data().name,
          'url': doc.data().url,
          'industry': doc.data().industry,
          'headquarters': doc.data().headquarters,
          'founder': doc.data().founder,
          'liaison': doc.data().liaison
        }
        this.companies.push(data)
      })
    })
  }
}
</script>




