<template>
  <div id="new-company">
    <h3>New Company</h3>
    <div class="row">
      <form @submit.prevent="saveCompany" class="col s12">
        <div class="row">
          <div class="input col s12">
            <input type="text" v-model="company_id" required>
            <label>Company ID#</label>
          </div>
        </div>
        <div class="row">
          <div class="input col s12">
            <input type="text" v-model="name" required>
            <label>Name</label>
          </div>
        </div>
        <div class="row">
          <div class="input col s12">
            <input type="text" v-model="url" required>
            <label>URL</label>
          </div>
        </div>
        <div class="row">
          <div class="input col s12">
            <input type="text" v-model="industry" required>
            <label>Industry</label>
          </div>
        </div>
        <div class="row">
          <div class="input col s12">
            <input type="text" v-model="founder" required>
            <label>Founder</label>
          </div>
        </div>
        <div class="row">
          <div class="input col s12">
            <input type="text" v-model="headquarters" required>
            <label>Headquarters</label>
          </div>
        </div>
        <div class="row">
          <div class="input col s12">
            <input type="text" v-model="liaison" required>
            <label>Liaison</label>
          </div>
        </div>
        <button type="submit" class="btn">Submit</button>
        <router-link to="/" class="btn grey">Cancel</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'new-company',
  data () {
    return {
      company_id: null,
      name: null,
      url: null,
      industry: null,
      headquarters: null,
      founder: null,
      liaison: null
    }
  }, 
  methods: {
    saveCompany () {
      db.collection('Companies').add({
        company_id: this.company_id,
        name: this.name,
        url: this.url,
        industry: this.industry,
        headquarters: this.headquarters,
        founder: this.founder,
        liaison: this.liaison
      }) 
      .then(docRef => this.$router.push('/'))
      .catch(error => console.log(err))
    }
  }
}
</script>




