<template>
  <div id="view-company">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>{{name}}</h4>
      </li>
      <li class="collection-item">Company ID#: {{company_id}}</li>
      <li class="collection-item">URL: {{url}}</li>
      <li class="collection-item">Industry: {{industry}}</li>
      <li class="collection-item">Headquarters: {{headquarters}}</li>
      <li class="collection-item">Founder: {{founder}}</li>
      <li class="collection-item">Liaison: {{liaison}}</li>
    </ul>
    <router-link to="/" class="btn grey">Back</router-link>
    <button @click="deleteCompany" class="btn red">Delete</button>
    <div class="fixed-action-btn">
      <router-link v-if="company_id" v-bind:to="{name: 'edit-company', params: {company_id: company_id}}" class="btn-floating btn-large red"> 
        <i class="fa fa-pencil-alt"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'view-company',
  data () {
    return {
      company_id: null,
      name: null,
      url: null,
      industry: null,
      headquarters: null,
      founder: null,
      liaison: null
    }
  },
  beforeRouteEnter (to, from, next) {
    db.collection('Companies').where('company_id', '==', to.params.company_id).get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        next(vm => {
          vm.company_id = doc.data().company_id
          vm.name = doc.data().name
          vm.url = doc.data().url
          vm.industry = doc.data().industry
          vm.headquarters = doc.data().headquarters
          vm.founder = doc.data().founder
          vm.liaison = doc.data().liaison
        }) 
      })
    })
  },
  watch: {
    '$route': 'fetchData'
  },
  //custom methods
  methods: {
    fetchData () {
      db.collection('Companies').where('company_id', "==", this.$route.params.company_id).get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          this.company_id = doc.data().company_id
          this.name = doc.data().name
          this.url = doc.data().url
          this.industry = doc.data().industry
          this.headquarters = doc.data().headquarters
          this.founder = doc.data().founder
          this.liaison = doc.data().liaison
        })
      })
    },
    deleteCompany () {
      if(confirm('Are you sure?')) {
        db.collection('Companies').where('company_id', "==", this.$route.params.company_id).get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          doc.ref.delete();
          this.$router.push('/') //router for redirect
        })
      })
      }
    }
  }
}
</script>




