<template>
  <div id="edit-company">
    <h3>Edit Company</h3>
    <div class="row">
      <form @submit.prevent="updateCompany" class="col s12">
        <div class="row">
          <div class="input col s12">
            <input disabled type="text" v-model="company_id" required>
          </div>
        </div>
        <div class="row">
          <div class="input col s12">
            <input type="text" v-model="name" required>
          </div>
        </div>
        <div class="row">
          <div class="input col s12">
            <input type="text" v-model="url" required>
          </div>
        </div>
        <div class="row">
          <div class="input col s12">
            <input type="text" v-model="industry" required>
          </div>
        </div>
        <div class="row">
          <div class="input col s12">
            <input type="text" v-model="founder" required>
          </div>
        </div>
        <div class="row">
          <div class="input col s12">
            <input type="text" v-model="headquarters" required>
          </div>
        </div>
        <div class="row">
          <div class="input col s12">
            <input type="text" v-model="liaison" required>
          </div>
        </div>
        <button type="submit" class="btn">Submit</button>
        <router-link to="/" class="btn grey">Cancel</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import db from  './firebaseInit'
export default {
  name: 'edit-company',
  data () {
    return {
      company_id: null,
      name: null,
      url: null,
      industry: null,
      headquarters: null,
      founder: null,
      liaison: null
    }
  },
  beforeRouteEnter (to, from, next) {
    db.collection('Companies').where('company_id', '==', to.params.company_id).get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        next(vm => {
          vm.company_id = doc.data().company_id
          vm.name = doc.data().name
          vm.url = doc.data().url
          vm.industry = doc.data().industry
          vm.headquarters = doc.data().headquarters
          vm.founder = doc.data().founder
          vm.liaison = doc.data().liaison
        }) 
      })
    })
  },
  watch: {
    '$route': 'fetchData'
  },
  //custom methods
  methods: {
    fetchData () {
      db.collection('Companies').where('company_id', "==", this.$route.params.company_id).get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          this.company_id = doc.data().company_id
          this.name = doc.data().name
          this.url = doc.data().url
          this.industry = doc.data().industry
          this.headquarters = doc.data().headquarters
          this.founder = doc.data().founder
          this.liaison = doc.data().liaison
        })
      })
    },
    updateCompany () {
      db.collection('Companies').where('company_id', "==", this.$route.params.company_id).get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          doc.ref.update({
            company_id: this.company_id,
            name: this.name,
            url: this.url,
            industry: this.industry,
            headquarters: this.headquarters,
            founder: this.founder,
            liaison: this.liaison
          }).then(() => {
            this.$router.push({
              name: 'view-company', params: {company_id: this.company_id}
            })
          })
        })
      })
    }
  }
}
</script>




